import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import hideCustomPasteButton from '../utils/hideCustomPasteButton';
import { isKeyHotkey } from 'is-hotkey';

const isMoveHotKey = (event: React.KeyboardEvent) => ['up', 'down', 'enter']
  .map((key) => isKeyHotkey(key))
  .some((isEvent) => isEvent(event));

export default function onKeyDown(
  event: React.KeyboardEvent,
  controller: Controller,
  next: Function,
) {
  const { customPanel } = controller.value.data;
  if (customPanel && isMoveHotKey(event)) {
    return event.preventDefault();
  }
  hideCustomPasteButton(controller);
  return next();
}
